<template>
  <div style="width: 100%;height: 100%;border-top: 2px solid #ccc;">
    <div class="tariffCards">
      <div @mouseout="mouseBlockFun" @mousemove="blockFun(index)" :class="item.style" v-for="(item, index) of listData" :key="index">
        <h3>
          {{item.title}}
        </h3>
        <span class="modul_size">
          I'm a module.
        </span>
      </div>
    </div>
    <p class="content_img_box animated"
       :class="{'lightSpeedIn': isClass, 'noneBlock': !isClass}">
      {{conSize}}
    </p>
  </div>
</template>

<script>
export default {
  name: 'conImg_01',
  props: {
    listData: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      conSize: '',
      isClass: false,
      numInt: parseInt(4 * Math.random()),
      styleC: ['economy', 'premiumeconomy', 'business', 'first']
    }
  },
  mounted () {

  },
  methods: {
    mouseBlockFun () {
      this.conSize = ''
      this.isClass = false
    },
    blockFun (index) {
      this.isClass = true
      this.conSize = this.listData[index].content
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .modul_size{
    font: .2rem name_font_family_03;
  }
  .content_img_box{
    position: absolute;
    border: 1px solid #fff;
    padding: 0.2rem 0.25rem;
    border-radius: 8px;
    right: 0;
    top: 28%;
    width: 3rem;
    right: 18%;
    background: rgba(0,0,0,.5);
    color: #fff;
  }
  .noneBlock{
    display: none;
  }
</style>



