                    <span class="dummy_title animated flip">Plug-in</span>
                  </div>
                  <div class="dummy-text">
                    <contentImg01 :listData="list06"></contentImg01>
                  </div>
                </div>
              </div>
              <span class="loading"></span>
              <span class="icon close-content"></span>
            </div>
