<script>
import contentImg01 from '@/components/content_img_01'
export default {
  name: 'home',
  components: {
    contentImg01
  },
  data () {
    return {
      list01: [
        {
          title: 'Canvas',
          style: 'economy',
          content: '了解 canvas 基本绘图api，实际应用为在svg地图上标点并实现鼠标交互事件。'
        },
        {
          title: '本地存储',
          style: 'premiumeconomy',
          content: '在项目中熟练使用 H5 的 localStorage/sessionStorage，localStorage - 为没有时间限制的数据存储，sessionStorage - 针对一个 session 的数据存储（会话级别）。'
        },
        {
          title: '语义化标签',
          style: 'business',
          content: '掌握H5新增语义化标签，并能熟练在项目中使用。'
        },
        {
          title: '表单控件',
          style: 'first',
          content: '对于新增的表单属性能够了解其作用，并能熟练在项目中使用。'
        }
      ],
      list02: [
        {
          title: '动画',
          style: 'economy',
          content: '能够熟练运用css3动画实现各种效果，掌握animate.css动画库。'
        },
        {
          title: '2D/3D转换',
          style: 'premiumeconomy',
          content: '运用css3 的 transform 属性熟练的完成元素的2D转换/3D转换。'
        },
        {
          title: '盒模型',
          style: 'business',
          content: '熟练运用并掌握css3弹性盒模型。'
        },
        {
          title: '选择器',
          style: 'first',
          content: '对应css3新增选择器能够很好的理解，并在项目中熟练应用。'
        }
      ],
      list03: [
        {
          title: 'ES6语法',
          style: 'economy',
          content: 'ES6语法'
        },
        {
          title: 'Ajax',
          style: 'premiumeconomy',
          content: 'Ajax'
        },
        {
          title: '闭包',
          style: 'business',
          content: '闭包'
        },
        {
          title: '面向对象',
          style: 'first',
          content: '面向对象'
        }
      ],
      list04: [
        {
          title: 'vue-router',
          style: 'economy',
          content: 'vue-router'
        },
        {
          title: 'vuex',
          style: 'premiumeconomy',
          content: 'vuex'
        },
        {
          title: 'axios',
          style: 'business',
          content: 'axios'
        },
        {
          title: 'webpack',
          style: 'first',
          content: 'webpack'
        }
      ],
      list05: [
        {
          title: 'iView',
          style: 'economy',
          content: 'iView'
        },
        {
          title: 'Element UI',
          style: 'premiumeconomy',
          content: 'Element UI'
        },
        {
          title: 'layui',
          style: 'business',
          content: 'layui'
        },
        {
          title: 'Bootstrap',
          style: 'first',
          module: 'form control',
          content: 'Bootstrap'
        }
      ],
      list06: [
        {
          title: 'zTree',
          style: 'economy',
          content: 'zTree'
        },
        {
          title: 'Echarts/G2',
          style: 'premiumeconomy',
          content: 'Echarts/G2'
        },
        {
          title: 'Swiper',
          style: 'business',
          content: 'Swiper'
        },
        {
          title: 'mCustomScrollbar',
          style: 'first',
          module: 'form control',
          content: 'mCustomScrollbar'
        }
      ],
      bannerSize: 'Hello word , hello 2019',
      showSize: '',
      flySize: '这世界上，并不是所有的东西都符合想象，有些时候，山是水的故事，云是风的故事；也有些时候，星不是夜的故事，情不是爱的故事。但是，无奈但又必须去接受。因为幸福，其实很简单。平静的呼吸，仔细的聆听，微笑着生活；有人爱，有事做，有所期待；',
      flyShowSize: '',
      flyArr: []
    }
  },
  mounted () {
    this.oSize()
    this.flyIn()
    gridAnimate()
  },
  methods: {
    oSize () {
      let num = 1
      let myTimer = setInterval(() => {
        this.showSize = this.bannerSize.substring(0, num)
        num++
        if (this.showSize === this.bannerSize) {
          clearInterval(myTimer)
        }
      }, 100)
    },
    flyIn () {
      let flyNum = 0
      let flyTimer = setInterval(() => {
        const randomN = Math.floor(Math.random() * 4) + 1
        let eff = ''
        switch (randomN) {
          case 1:
            eff = 'fadeInRightBig'
            break
          case 2:
            eff = 'fadeInLeftBig'
            break
          case 3:
            eff = 'fadeInUpBig'
            break
          case 4:
            eff = 'fadeInDownBig'
            break
        }
        flyNum++
        const sizeList = this.flySize.substring(flyNum - 1, flyNum)
        this.flyShowSize = sizeList
        const oHtml = '<span style="display: inline-block;" class="introduce animated ' + eff + '">' + sizeList + '</span>'
        $('.banner_index_content').append(oHtml)
        if (flyNum === this.flySize.length) {
          clearInterval(flyTimer)
        }
      }, 20)
    }
  }
}
</script>
<style scoped>
  .banner_index_icon{
    width: 3.5rem;
    height: 3rem;
    /*background: url("../../assets/images/imac_mock-up.png") no-repeat;*/
    /*background-size: 100% 100%;*/
    position: absolute;
    right: 10%;
    top: 50%;
    margin-top: -2rem;
  }
  @keyframes mymove
  {
    0% {right:0rem;top:50%;}
    25% {right:2rem;top:30%;}
    50% {right:4rem;top:50%;}
    75% {right:2rem;top:60%;}
    100% {right:0rem;top:50%;}
  }
  @-webkit-keyframes mymove /*Safari and Chrome*/
  {
    0% {right:0rem;top:50%;}
    25% {right:2rem;top:30%;}
    50% {right:4rem;top:50%;}
    75% {right:2rem;top:60%;}
    100% {right:0rem;top:50%;}
  }
  .banner_index_title{
    width: 50%;
    height: 100%;
  }
  .banner_index_title > span{
    width: 100%;
    display: inline-block;
    text-align: center;
    font: .4rem name_font_family_02;
    color: #ffffff;
    text-shadow: .01rem .04rem .02rem #333;
    margin-top: .6rem;
  }
  .banner_index_content{
    width: 50%;
    height: auto;
    margin: 0 auto;
    padding: .2rem;
    background: rgba(255,255,255,.1);
    border-radius: 6px;
    box-shadow: 0px 0px 10px #fff;
    margin-top: .4rem;
    overflow: hidden;
  }
  .skill_index{
    width: 100%;
    height: auto;
    background: url("../../assets/images/plate01Bg_02.jpg");
    position: relative;
    padding-bottom: .5rem;
  }
  .grid3d{
    width: 100%;
    padding-top: .5rem;
  }
  .grid figure{
    position: relative;
  }
  .grid figure, .grid .placeholder {
    width: 30%;
    height: 2.12rem;
    overflow: hidden;
  }
  .grid figure, .grid .placeholder{
    border-radius: 6px;
  }
  .grid figure img{
    height: 100%;
    transition: all .5s;
    ransform: scale(1);
  }
  .grid figure .zzc_bg{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: rgba(20,133,239,.8);
    transform: translateX(-100%);
    transition: all .5s;
    display: inline-block;
    z-index: 999;
    text-align: center;
  }
  .grid figure .zzc_bg span{
    display: inline-block;
    line-height: 2.12rem;
    color: #ffffff;
    font: .6rem name_font_family_01;
    margin-top: .7rem;
  }
  .grid figure:hover .gridImg{
    transform: scale(1.3);
  }
  .grid figure:hover .zzc_bg{
    transform: translateX(0%);
  }

  .skill_index_title{
    width: 100%;
    text-align: center;
  }
  .skill_index_title span{
    display: inline-block;
    vertical-align: middle;
    margin-top: .1rem;
    font-size: 24px;
    color: #333333;
  }
  .skill_index_title i{
    display: inline-block;
    vertical-align: middle;
    width: .3rem;
    height: .02rem;
    background-color: rgba(0,0,0,.5);
    margin: .1rem .1rem 0 .1rem;
    animation:mymove 3s linear infinite alternate;
    -webkit-animation:mymove 3s linear infinite alternate; /*Safari and Chrome*/
  }
  @keyframes mymove
  {
    0% {transform:rotate(0deg) translate(0px,0px) scale(1);opacity: 1}
    50% {transform:rotate(180deg) translate(40px,0px) scale(0);opacity: 0}
    100% {transform:rotate(360deg) translate(0px,0px) scale(1);opacity: 1}
  }
  .skill_index_title p{
    font: 26px name_font_family_01;
  }
</style>



